@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.header {
  /* display: flex;
  align-items: center; */
  justify-content: space-evenly;
  flex-direction: column;
  height: 35vh;
  width: 100%;
}

.title {
  font-size: 7vw;
  display: flex;
  justify-content: center;
  text-transform: uppercase;
  font-family: 'Montserrat', sans-serif;
}

.inputs {
  width: 100%;
  display: flex;
  justify-content: space-around;
}

@media (max-width: 900px) {
  .header {
    justify-content: space-evenly;
    height: 25vh;
  }

  .title {
    font-size: 7vw;
  }

  .inputs {
    width: 100%;
    display: flex;
    justify-content: space-around;
  }

  .search,
  select {
    width: 43%;
  }
}

.meanings {
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
  scrollbar-width: thin;
  height: 55vh;
  border: 10px solid rgba(105, 105, 105);
  border-radius: 10px;
  padding: 10px 20px;
  overflow-x: hidden;
  margin-top: 3rem;
}

.subTitle {
  font-size: 5vw;
  font-family: 'montserrat', sans-serif;
}

.audio {
  margin: 1rem;
}

@media (max-width: 900px) {
  .meanings {
    height: 60vh;
    overflow-x: hidden;
    overflow-y: scroll;
  }

  .singleMean {
    display: flez;
    flex-direction: column;
    border-radius: 10px;
    padding: 10px 20px;
    margin: 10px 0;
  }
}

